@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap');
*{
  margin: 0;
}
:root{
  --primary-color:#F55A5A;
  --primary-font-family:'Roboto', sans-serif;
  --secondary-font-family: 'Lato', sans-serif;
  --primary-font-color:#000;
  --secondary-font-color:#2B283A;
  --primary-font-size:clamp(1.1em, 3.5vw, 3.5rem);
  --secondary-font-size:clamp(0.8em, 1.5vw, 1.1rem);
}

/*navbar style*/
.nav-bar{
  background-color: var(--primary-color);
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: var(--primary-font-family);
  padding: 1rem 0;
}
.nav-text{
  font-size: clamp(1em, 1.7vw, 1.8rem);
  margin-left: 0.8rem;
  letter-spacing: 0.15rem;
}
.nav-icon{
  margin-right: 0.8rem;
}

/* travel journal card style */
.travel-card-container{
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 2.7rem auto;
}
.travel-card{
  display: flex;
  flex-direction: row;
  width: 95%;
  margin: 3rem auto;
  justify-content: space-between;
}
.card-image{
  width: 20%;
  
}
.img{
  width: 100%;
  border: 1px solid transparent;
  border-radius: 0.6rem;
}
.card-info{
  width: 70%;
  margin: auto 0;

}
.location-container{
  display: flex;
  flex-direction: row;
  font-family: var(--secondary-font-family);
  font-size: var(--secondary-font-size);
}
.location-icon{
  width: 13px;
  margin-right: 0.4rem;
  
}
.location-on-map{
  margin: 0 0.9rem;
  color: rgb(130, 130, 130);
}
.travel-location-name{
  color: #000;
  font-family: var(--primary-font-family);
  font-size: var(--primary-font-size);
  margin: 0.6rem 0;
}
.travel-time{
  font-family: var(--primary-font-family);
  color: rgb(18, 18, 18);
  font-size: var(--secondary-font-size);
  margin-top: 1.8rem;
}
.travel-info-text{
  font-family: var(--secondary-font-family);
  color: var(--secondary-font-color);
  font-size: var(--secondary-font-size);
  word-spacing: 0.24rem;
  line-height: 1.58rem;
  margin-top: 0.7rem;
}

/* responsive design style */
@media (max-width: 650px){
  .card-image{
    width: 40%;
  }
  .card-info{
    width: 50%;
  }
}
@media (max-width:550px){
  body{
    background-color: rgb(249, 249, 249);
  }
  .travel-card{
    flex-direction: column;
    background-color: white;
    padding: 2rem 0.7rem;
    box-shadow: 0 0 14px -5px #aaa;
    border-radius: 0.7rem;
  }
  .nav-icon{
    width: 6%;
    margin-right: 0.2rem;
  }
  .card-image,.card-info{
    width: 95%;
    text-align: center;
  }
  .card-image{
    margin: 0 auto;
  }
  .location-container{
    flex-direction: column;
    justify-content: center;
  }
  .location-icon{
    display: none;
  }
}